
import { getArtistsApi, getSubListApi, getTop50Api, subArtistApi } from "@/api/artist";
import { likeMusic } from "@/api/discover";
import { getSongUrlApi } from "@/api/song";
import CollapseSiderIcon from "@/components/svgIcons/CollapseSiderIcon.vue";
import CollectBoxIcon from "@/components/svgIcons/CollectBoxIcon.vue";
import CollectListIcon from "@/components/svgIcons/CollectListIcon.vue";
import HorizontalNav from "@/components/viewParts/HorizontalNav.vue";
import { downloadMusic } from "@/utils/file";
import { playTime } from "@/utils/filters";
import { AxiosResponseProps } from "@/utils/request";
import { computed, defineComponent, getCurrentInstance, reactive, ref, toRefs } from "vue";
import { useRoute } from "vue-router";
import { useStore } from "vuex";
export default defineComponent({
	components: { HorizontalNav, CollectListIcon, CollapseSiderIcon, CollectBoxIcon },
	setup() {
		const route = useRoute();
		const store = useStore();
		const id = route.query.id as string;
		const state = reactive({
			artist: {},
			hotSongs: [],
			navs: [
				{
					title: "专辑",
					active: true,
				},
				{
					title: "MV",
				},
				{
					title: "歌手详情",
				},
				{
					title: "相似歌手",
				},
			],
			realSongs: [],
			songs: [],
			subList: [],
			columns: [
				{
					span: 2,
					dataIndex: "toolBar",
					slots: { customRender: "toolBar" },
					colItemStyle: { "text-align": "center" },
				},
				{
					span: 9,
					dataIndex: "name",
				},
				{
					span: 1,
					dataIndex: "dt",
					render: (text) => {
						return playTime(text);
					},
				},
			],
			likedMusicList: computed(() => store.state.likedMusicList),
			playAll: ref(null),
		});
		const isSubed = computed(() => state.subList.some((item) => item.id === id));
		const {
			appContext: {
				config: {
					globalProperties: { $toast },
				},
			},
		} = getCurrentInstance();
		interface ArtistResponse extends AxiosResponseProps {
			artist: any[];
			hotSongs: any[];
		}
		const getArtist = () => {
			getArtistsApi(id).then((res: any) => {
				if (res.code === 200) {
					state.artist = res.artist;
					state.hotSongs = res.hotSongs;
				}
			});
		};
		getArtist();
		interface Top50Response extends AxiosResponseProps {
			songs: any[];
		}
		const getTop50 = () => {
			getTop50Api(id).then((res: Partial<Top50Response>) => {
				if (res.code === 200) {
					state.songs = res.songs.slice(0, 10);
					state.realSongs = res.songs;
					console.log(state.songs);
				}
			});
		};
		getTop50();
		const getSubList = () => {
			return getSubListApi().then((res: any) => {
				if (res.code === 200) {
					state.subList = res.data;
				}
			});
		};
		getSubList();
		const subArtist = (t) => {
			subArtistApi({ id, t })
				.then(async (res: AxiosResponseProps) => {
					if (res.code === 200) {
						await getSubList();
						if (t) {
							$toast.success("收藏成功");
						} else {
							$toast.success("已取消收藏");
						}
					}
				})
				.catch((err) => {
					console.dir(err);
					const response = err?.response;
					const status = response?.status;
					const code = response?.data?.code;
					const blockText = response?.data?.data?.blockText;
					if (status === 400 && code === -462) {
						console.warn(blockText);
						$toast.error(blockText);
					}
				});
		};
		const collectMusic = (like, id) => {
			likeMusic({ id, like }).then(async (res: any) => {
				if (res.code === 200) {
					await store.dispatch("getLikedMusicList");
					$toast.success(like ? "已添加到我喜欢的音乐" : "取消喜欢成功");
				}
			});
		};
		const handleDownloadMusic = ({ id, name }) => {
			getSongUrlApi({ id }).then((res: AxiosResponseProps) => {
				if (res.code === 200) {
					let urls = res.data;
					if (urls && urls.length) {
						downloadMusic(urls[0].url, name)
							.then(() => {
								$toast.success("已添加至下载");
							})
							.catch(() => {
								$toast.warn("下载失败");
							});
					} else {
						$toast.warn("获取音乐链接失败");
					}
				}
			});
		};
		return {
			...toRefs(state),
			isSubed,
			subArtist,
			collectMusic,
			handleDownloadMusic,
		};
	},
});
